.popup {  
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    position: fixed;
    background-color: rgb(0 0 0 / 0.5);
    opacity: 0;
    visibility: hidden;
    transition: all .6s ease-in-out 0s;
  }

  